
import styled from 'styled-components'
import React from 'react'
import { StaticQuery, Link, graphql  } from 'gatsby'
import { heavy, veryHeavy } from './colors';

const OuterContainer = styled.div`
  margin-bottom: 24px;
`;

const HeaderContainer = styled.div`
  width: 100%;
  height: 48px;
  border-bottom-style: solid;
  border-bottom-width: 1px;

  display: flex;
  flex-direction: row;
`;

const LeftTitle = styled(Link)`
  line-height: 48px;
  color: ${heavy};
  font-weight: bold;
  font-size: 24px;
  padding-left: 24px;
  margin-right: auto;
  text-decoration: none;

  &:hover {
    cursor: pointer;
  }

  &:visited {
    color: ${heavy};
  }
`;

const RightArea = styled.div`
  padding-right: 24px;

  display: flex;
`;

const RightButton = styled(Link)`
  height: 48px;
  line-height: 48px;
  color: ${veryHeavy};
  margin-left: auto;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 16px;
  text-decoration: none;

  &:hover {
    cursor: pointer;
  }

  &:visited {
    color: ${veryHeavy};
  }
`;

const PostHeader = ({ headerImage }) => {
  return (
    <StaticQuery
      query={graphql`
        query {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => {
        return (
          <OuterContainer>
            <HeaderContainer>
              <LeftTitle to="/">
                {data.site.siteMetadata.title}
              </LeftTitle>
              <RightArea>
                <RightButton to="/articles">
                  文章
                </RightButton>
                <RightButton to="/aboutme">
                  关于我
                </RightButton>
                <RightButton to="/rss">
                  RSS
                </RightButton>
              </RightArea>
            </HeaderContainer>
            {headerImage}
          </OuterContainer>
        );
      }}
    />
  );
}

export default PostHeader
